
.page-navigator {
  margin-top: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.page-pre, .page-next, .page {
  width: 24px;
  height: 24px;
  display: inline-block;
  cursor: pointer;
}
.page-pre, .page-next {
  background-size: 100% 100%;
}
.page-pre {
  background: url("../../../assets/imgs/dxyk/arrow-left.png") no-repeat;
}
.page-next {
  background: url("../../../assets/imgs/dxyk/arrow-right.png") no-repeat;
  margin-left: 30px;
}
.page {
  text-align: center;
  line-height: 24px;
  font-size: 14px;
  border-radius: 50%;
}
.page:nth-child(2) {
    margin-left: 30px;
}
.page, .diandiandian {
  margin-left: 20px;
}
.page-unactive {
  border: 1px solid #fff;
  color: #666666;
}
.page-unactive:hover {
    border-color: #2caa3f;
    color: #2caa3f;
}
.page-active {
  color: #fff;
  background-color: #2caa3f;
}



