
.dxyk-body-main-module2 {
  float: right;
  width: 250px;
  height: 100%;
  position: relative;
}
.dxyk-body-main-module2-d1, .dxyk-body-main-module2-d2, .dxyk-body-main-module2-d3 {
  position: absolute;
  border: 1px solid #eeeeee;
  width: 240px;
  height: 250px;
  background-color: #fff;
}
.dxyk-body-main-module2-d1 {
  z-index: 3;
  top: 0;
  left: 0;
  padding-top: 20px;
}
.dxyk-body-main-module2-d2 {
  z-index: 2;
  top: 4px;
  left: 4px;
}
.dxyk-body-main-module2-d3 {
  z-index: 1;
  top: 8px;
  left: 8px;
}
.dxyk-body-main-module2-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding-right: 20px;
}
.dxyk-body-main-module2-header-d1 {
    width: 90px;
    font-family: PingFangSC-Medium, sans-serif;
    background-color: #2caa3f;
    color: #fff;
    height: 36px;
    line-height: 36px;
    text-align: center;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}
.dxyk-body-main-module2-header-d2 {
    font-size: 12px;
    color: #999999;
}
.dxyk-body-main-module2-main-share {
  margin-top: 14px;
  padding: 0 14px;
}
.dxyk-body-main-module2-main-share-time {
    color: #adadad;
    font-size: 12px;
}
.dxyk-body-main-module2-main-share-name {
    position: relative;
    margin-top: 8px;
    color: #666666;
    font-size: 14px;
    vertical-align: middle;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 12px;
}
.dxyk-body-main-module2-main-share-name span {
      cursor: pointer;
}
.dxyk-body-main-module2-main-share-name span:hover {
        color: #2caa3f;
}
.dxyk-body-main-module2-main-share-name:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -3px;
      background-color: #ff9a41;
      border-radius: 50%;
      width: 6px;
      height: 6px;
      cursor: pointer;
}
.dxyk-body-main-module2-main-share-name:hover:before {
      background-color: #2caa3f;
}



