
.dxyk-body-main-module1 {
  float: left;
}
.dxyk-body-main-shares li {
  margin-top: 26px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.dxyk-body-main-shares li:first-child {
  margin-top: 0;
}
.dxyk-share-time {
  font-size: 14px;
  color: #adadad;
}
.dxyk-share-name {
  position: relative;
  font-size: 16px;
  color: #666666;
  margin-left: 20px;
  padding-left: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
}
.dxyk-share-name:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -3px;
    background-color: #d6d6d6;
    border-radius: 50%;
    width: 6px;
    height: 6px;
}
.dxyk-share-name:hover {
    color: #2caa3f;
}
.dxyk-share-name:hover:before {
    background-color: #2caa3f;
}



