
.dxyk-body {
  margin: 40px auto;
  background-color: #fff;
  width: 80%;
  padding: 56px 80px;
}
.dxyk-body-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.dxyk-filter span {
  display: inline-block;
  color: #c0c0c0;
  border: 1px solid #c0c0c0;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 16px;
  margin-left: 20px;
  cursor: pointer;
}
.dxyk-filter span:first-child {
  margin-left: 0;
}
.dxyk-filter span:hover {
  color: #72c67f;
  border-color: #72c67f;
}
.dxyk-search {
  width: 200px;
  position: relative;
}
.dxyk-search input {
    width: 100%;
    border: 1px solid #eeeeee;
    padding: 10px 40px 10px 14px;
    border-radius: 20px;
    outline: none;
}
.dxyk-search input:focus {
    border-color: #cccccc;
}
.dxyk-search-icon {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 50px;
    background: url("../../../assets/imgs/dxyk/search.png") no-repeat;
    background-position: center center;
    cursor: pointer;
}
.dxyk-body-main {
  margin-top: 30px;
  padding-top: 40px;
  border-top: 1px solid #eeeeee;
}



