
.dxyk-header {
  color: #fff;
  padding-top: 70px;
}
@media screen and (max-width: 1280px) {
.dxyk-header {
    padding-top: 20px;
}
}
@media screen and (min-width: 1281px) and (max-width: 1440px) {
.dxyk-header {
    padding-top: 30px;
}
}
@media screen and (min-width: 1441px) and (max-width: 1919px) {
.dxyk-header {
    padding-top: 50px;
}
}
.dxyk-header-logo {
  text-align: center;
}
.dxyk-header-logo img {
    width: 88px;
    height: 88px;
}
.dxyk-header-text {
  text-align: center;
  margin-top: 16px;
  font-size: 20px;
}



